.bannerSobre {
  background-image: url("../../../public/assets/banner-sobre.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 65vh;
  margin-top: 65px;
  text-align: center;
}

.bannerSobre h1 {
  font-size: 80px;
  color: #fff;
  text-shadow: 3px 3px 3px #000;
  word-spacing: 20px;
  letter-spacing: 3px;
  margin: 0 10px;
  padding: 0 20px;
}

/* DARK MODE SOBRE */
.darkSobre {
  background-color: #252525;
  color: #ececec;
}
.lightSobre {
  background-color: #ececec;
  color: #252525;
}

.textoSobre {
  text-align: center;
  padding: 80px 20px;
}

.textoSobre h2 {
  font-size: 40px;
  font-weight: 900;
}

.textoSobre span {
  font-weight: bold;
  font-style: italic;
  font-size: 20px;
}

.textoSobre p {
  margin: 20px auto;
  padding: auto 20px;
  font-size: 18px;
}

.textoSobre p:first-of-type {
  margin-top: 50px;
}

.imagensSobre {
  display: flex;
  align-items: center;
  justify-content: center;
}

.imagensSobre img {
  width: 50%;
  max-width: 800px;
  height: 38vh;
  object-fit: cover;
}

/* RESPONSIVO */
@media (max-width: 425px) {
  .bannerSobre {
    margin-top: 120px;
  }

  .bannerSobre h1 {
    font-size: 40px !important;
  }
}

@media (max-width: 768px) {
  .imagensSobre {
    flex-direction: column;
  }

  .bannerSobre h1 {
    font-size: 60px;
  }
  .imagensSobre img {
    width: 100%;
  }
}

/* ANIMAÇÃO TITULO */

.scale-up-hor-center {
  -webkit-animation: scale-up-hor-center 1s cubic-bezier(0.39, 0.575, 0.565, 1)
    both;
  animation: scale-up-hor-center 1s cubic-bezier(0.39, 0.575, 0.565, 1) both;
}

/**
 * ----------------------------------------
 * animation scale-up-hor-center
 * ----------------------------------------
 */
@-webkit-keyframes scale-up-hor-center {
  0% {
    -webkit-transform: scaleX(0.4);
    transform: scaleX(0.4);
  }
  100% {
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
  }
}
@keyframes scale-up-hor-center {
  0% {
    -webkit-transform: scaleX(0.4);
    transform: scaleX(0.4);
  }
  100% {
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
  }
}
