.rodape {
  height: 250px;
  display: flex;
  position: relative;
  flex-direction: column;
  justify-content: space-evenly;
  box-shadow: 0 -3px 10px #ececec50;
  z-index: 2;
}

/* DARK MODE RODAPE */

.darkRodape,
.darkRodape a {
  background-color: #252525;
  color: #ececec;
}
.darkRodape {
}
.lightRodape,
.lightRodape a {
  background-color: #f9db8a;
  color: #252525;
}

/* -------------- */

.blocos-rodape {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  padding: 10px 20px;
}

.blocos-rodape img {
  width: 80px;
}

.assinatura {
  text-align: center;
  font-size: 13px;
  font-style: italic;
  padding-bottom: 20px;
}

.bloco {
  max-width: 230px;
  margin: 30px 10px;
}

.bloco h4 {
  font-weight: 700;
  font-size: 16px;
  margin-bottom: 5px;
}
.bloco p {
  font-size: 13px;
  margin-bottom: 5px;
}
.bloco a {
  text-decoration: none;
}

@media (max-width: 768px) {
  .rodape {
    height: auto;
    padding-top: 20px;
  }
  .blocos-rodape {
    flex-direction: column;
    text-align: center;
    height: auto;
    padding: 20px 0;
  }
}
