.bannerSabores {
  background-image: url("../../../public/assets/banner-sabores.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 65vh;
  margin-top: 65px;
  text-align: center;
}

.bannerSabores h1 {
  font-size: 80px;
  text-transform: uppercase;
  color: #fff;
  text-shadow: 3px 3px 3px #000;
  word-spacing: 20px;
  letter-spacing: 3px;
  margin: 0 10px;
}

.saboresOpcoes {
  padding: 80px 0;
}

.saboresOpcoes h2 {
  text-align: center;
  font-size: 30px;
  padding: 25px;
  font-weight: bolder;
}

.saboresCards {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: baseline;
  width: calc(100% - 20px) / 3;
}

/* DARK MODE SABORES */
.darkSabores {
  background-color: #252525;
  color: #ececec;
}
.lightMode {
  background-color: #ececec;
  color: #252525;
}

.saboresCards .card {
  width: 30%;
  padding: 40px 10px;
  display: flex;
  flex-direction: column;
  justify-content: end;
  align-items: center;
  text-align: center;
  box-shadow: 2px 2px 5px 5px #dddcdc;
  border-radius: 5px;
  margin: 12px;
}

.card img {
  width: 100%;
  height: 250px;
  object-fit: cover;
  margin: 20px 0;
}

.saboresCards h3 {
  font-weight: bolder;
  font-size: 19px;
  padding: 10px 0;
}

.saboresCards p {
  font-size: 16px;
}

@media (max-width: 425px) {
  .saboresOpcoes {
    margin: 40px 0;
  }

  .bannerSabores {
    margin-top: 120px;
  }

  .bannerSabores h1 {
    font-size: 55px;
  }
}

@media (max-width: 768px) {
  .saboresCards {
    width: 100%;
  }

  .saboresCards .card {
    width: 100%;
    margin: 15px 40px;
  }
  .card img {
    object-fit: contain;
  }
}
@media (min-width: 769px) and (max-width: 935px) {
  .saboresCards {
    width: calc(100% - 20px) / 2;
  }

  .saboresCards .card {
    width: 45%;
  }
  .card img {
    object-fit: contain;
  }
}

/* ANIMAÇÃO TITULO */

.scale-up-hor-center {
  -webkit-animation: scale-up-hor-center 1s cubic-bezier(0.39, 0.575, 0.565, 1)
    both;
  animation: scale-up-hor-center 1s cubic-bezier(0.39, 0.575, 0.565, 1) both;
}

/**
 * ----------------------------------------
 * animation scale-up-hor-center
 * ----------------------------------------
 */
@-webkit-keyframes scale-up-hor-center {
  0% {
    -webkit-transform: scaleX(0.4);
    transform: scaleX(0.4);
  }
  100% {
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
  }
}
@keyframes scale-up-hor-center {
  0% {
    -webkit-transform: scaleX(0.4);
    transform: scaleX(0.4);
  }
  100% {
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
  }
}
