#topo {
  width: 100%;
  height: 65px;
  position: fixed;
  margin-top: -65px;
  z-index: 1;
  box-shadow: 0 3px 10px #ececec50;
}

.light-topo,
.btn-theme {
  background-color: #f9db82;
}

.dark-topo,
.btn-theme {
  background-color: #252525;
}

.topo-elementos {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.topo-elementos .logo {
  width: 70px;
  margin: 0 10px;
}

.topo-links .link {
  text-decoration: none;
  padding: 5px 10px;
  margin: 0 5px;
  font-size: 18px;
  font-weight: bold;
  cursor: pointer;
  text-shadow: 1px 1px 2px #cacaca;
  transition: 0.5s;
}

.light-link {
  color: #000;
}
.dark-link {
  color: #f9db82;
}

.topo-links .link:hover {
  color: #fff;
  text-shadow: 2px 2px 2px #cacaca;
  transform: scale(1.2);
}

.topo-objetos {
  display: flex;
  align-items: center;
  justify-content: center;
}

.btn-img {
  width: 25px;
  height: 25px;
  object-fit: contain;
}

.btn-theme {
  display: flex;
  border: none;
  margin: 5px 10px 5px 5px;
  border-radius: 25px;
  box-shadow: 2px 2px 2px #7c7c7c;
  transition: 0.6s;
}
.btn-theme:hover {
  transform: scale(1.2);
  box-sizing: border-box;
  cursor: pointer;
}
.botao {
  display: none;
  padding: 5px;
  border-radius: 5px;
}

@media (max-width: 425px) {
  #topo {
    height: 120px;
    margin-top: -120px;
  }

  .topo-elementos {
    flex-direction: column;
  }

  .topo-links {
    padding: 10px;
    width: 100%;
    text-align: center;
    border-top: 1px solid #7f7042;
  }
}
