.mainBanner {
  background-image: url("../../../public/assets/banner-home.png");
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: 90vh;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 90vh;
  margin-top: 65px;
  text-align: center;
}

/* DARK MODE BANNER */
.darkBanner {
  background-color: #252525;
}
.lightBanner {
  background-color: #f9db82;
}
/* DARK MODE TEXTOS */
.darkTexto {
  background-color: #252525;
  color: #ececec;
}

.lightTexto {
  background-color: #ececec;
  color: #252525;
}

.mainBanner h1 {
  font-size: 80px;
  text-transform: uppercase;
  color: #ececec;
  text-shadow: 3px 3px 3px #000;
  word-spacing: 20px;
  letter-spacing: 3px;
  margin: 0 10px;
}

.mainBox,
.mainBox-texto {
  width: 50%;
  height: 550px;
}
.mainBox-texto {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px 80px;
}

.mainBox-texto h2,
.mainBox-texto span {
  text-align: center;
}

.mainSabores,
.mainEventos,
.mainSobre {
  display: flex;
  justify-content: center;
  align-items: stretch;
}

main img {
  object-fit: cover;
}
.mainBox-texto h2 {
  font-size: 30px;
}

.mainBox-texto p {
  margin-top: 20px;
  font-size: 16px;
}

.mainBox-texto span {
  font-size: 15px;
  font-weight: bold;
  font-style: italic;
}

@media (max-width: 425px) {
  .mainBanner {
    margin-top: 120px;
  }

  .mainBanner h1 {
    font-size: 55px;
  }

  .mainBox-texto {
    padding: 20px 40px;
  }

  .mainBox {
    height: 300px;
  }
}

@media (max-width: 768px) {
  .mainBanner h1 {
    font-size: 60px;
  }

  main img {
    object-fit: fill;
  }

  .mainSabores,
  .mainEventos,
  .mainSobre {
    flex-direction: column;
  }

  .mainBox,
  .mainBox-texto {
    width: 100%;
  }

  .mainEventos {
    flex-direction: column-reverse;
  }
}

/* ANIMAÇÃO TITULO */

.scale-up-hor-center {
  -webkit-animation: scale-up-hor-center 1s cubic-bezier(0.39, 0.575, 0.565, 1)
    both;
  animation: scale-up-hor-center 1s cubic-bezier(0.39, 0.575, 0.565, 1) both;
}

/**
 * ----------------------------------------
 * animation scale-up-hor-center
 * ----------------------------------------
 */
@-webkit-keyframes scale-up-hor-center {
  0% {
    -webkit-transform: scaleX(0.4);
    transform: scaleX(0.4);
  }
  100% {
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
  }
}
@keyframes scale-up-hor-center {
  0% {
    -webkit-transform: scaleX(0.4);
    transform: scaleX(0.4);
  }
  100% {
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
  }
}
